import React from 'react'
import '../../assets/styles/global.scss'
import { CommonLoading } from 'react-loadingg'
import { Grid, Row, Column } from '@mycv/mycv-grid'
import config from '../../config'
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'
import PageNotFound from '../PageNotFound'
import PostDetail from '../../container/PostDetail'
const Header = React.lazy(() => import('../../container/Header'))
const Home = React.lazy(() => import('../../container/Home'))
const FollowContainer = React.lazy(() => import('../../container/FollowContainer'))
const Profile = React.lazy(() => import('../../container/Profile'))
const Sidebar = React.lazy(() => import('../../container/Sidebar'))
const UploadFile = React.lazy(() => import('../../container/UploadFile'))

const App = () => {
  return (
    <Router>
      <Grid>
        <React.Suspense fallback={<CommonLoading />}>
          <Header />
          <Grid type="wide" maxWidth={config.mainWidth}>
            <Switch>
              <Route path={config.routes.upload} component={UploadFile}></Route>
              <Route>
                <Row>
                  <Column size={0} sizeTablet={0} sizeDesktop={4}>
                    <Sidebar />
                  </Column>
                  <Column size={12} sizeTablet={12} sizeDesktop={8}>
                    <Switch>
                      <Route exact path={config.routes.home} component={Home}></Route>
                      <Route path={config.routes.follow} component={FollowContainer}></Route>
                      <Route path={config.routes.postDetail} component={PostDetail}></Route>
                      <Route path={config.routes.profile} component={Profile}></Route>
                    </Switch>
                  </Column>
                </Row>
              </Route>
            </Switch>
          </Grid>
        </React.Suspense>
      </Grid>
    </Router>
  )
}

export default App
