import { HIDE_MODAL, LOGIN_MODAL, REGISTER_MODAL } from "../actions/constant"

const initialState = {
  modalLogin: null,
  modalRegister: null,
  isHideModal: false
}

const modalReducer = (state = initialState, action) => {
  switch (action.type) {
    case LOGIN_MODAL: {
      return {
        modalLogin: LOGIN_MODAL
      }
    }
    case REGISTER_MODAL: {
      return {
        modalRegister: REGISTER_MODAL
      }
    }
    case HIDE_MODAL: {
      return {
        isHideModal: false
      }
    }
    default: {
      return state
    }
  }
}

export default modalReducer
