import { FETCH_USER, FETCH_USER_FAILED, FETCH_USER_SUCCESS } from '../actions/constant'

const initialState = {
  loading: false,
  error: null,
  user: null
}

const avatarReducer = (state = initialState, action) => {
  switch (action.type) {
    case FETCH_USER: {
      return {
        loading: true,
        avatar: null,
        error: null
      }
    }
    case FETCH_USER_SUCCESS: {
      return {
        loading: false,
        user: action.payload.user,
        error: null
      }
    }
    case FETCH_USER_FAILED: {
      return {
        loading: false,
        user: null,
        error: action.payload.error
      }
    }
    default: {
      return state
    }
  }
}
export default avatarReducer
